</template>

<script>
import axios from 'axios'

 export default {
    data() {
      return {
        tableData: []
      }
    },
    created(){
        axios.get('retrieve/test',{       // url
        }).then(function(res){
            // this.tableData
            this.tableData = res.data.result;
            //       console.log(this.tableData);
            // var obj = JSON.parse(res.data);
        console.log(this.tableData);
        }).catch(function (error) {
            console.log(error);
        });
  
}
  }
</script>
